<template>
    <div>
        <div></div>
        <div>{{$route.params}}</div>
        <div>
            <el-form :model="form" label-width="100px">
                <el-form-item label="" prop="crowdName">
                    <el-input v-model="form.crowdName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pCrowdName">
                    <el-select v-model="form.pCrowdName">
                        <el-option value="a">a</el-option>
                        <el-option value="b">b</el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="form.conditions"></el-input>
                </el-form-item>
                <el-form-item label="" prop="validPeriod">
                    <el-date-picker
                            v-model="form.validPeriod"
                            type="datetimerange"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            value-format="yyyy-MM-dd HH:mm"
                            format="yyyy-MM-dd HH:mm"
                            :picker-options="pickerOptions"
                            :default-time="['00:00:00', '23:59:59']"
                    >
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Add",
        data () {
            return {
                form: {
                    crowdName: '',
                    pCrowdName: '',
                    conditions: undefined,
                    validPeriod: undefined
                },
                pickerOptions: {
                    disabledDate(time) {
                        // 60
                        const curDate = (new Date()).getTime()
                        // 30
                        const day = 60 * 24 * 3600 * 1000
                        const dateRange = curDate + day
                        return time.getTime() < Date.now() - 24 * 60 * 60 * 1000 || time.getTime() > dateRange
                    }
                }
            }
        }
    }
</script>

